import uniSegmentedControl from "@/components/uni-segmented-control/uni-segmented-control.vue"
export default {
	components: {
		uniSegmentedControl
	},
	data() {
		return {
			list: [],
			items: ['上架商品', '下架商品'],
			current: 0
		}
	},
	onLoad() {
		let mid = this.getMerchantInfo().id;
		if(mid){
			this.getRoomList("E");
		}else{
			uni.showToast({
				title:"您还未认证商家",
				icon:"none"
			})
			setTimeout(()=>{
				uni.switchTab({
					url:"../tabbar/data"
				})
			},1000)
		}
	},
	methods: {
		getRoomList(status) {
			uni.request({
				url: this.prefix + "/getAllRoom",
				data: {
					merchantId: this.getMerchantInfo().id,
					status: status,
					size: 50
				},
				success: (res) => {
					this.list = res.data.data;
				}
			})
		},
		pull(id,index) {
			uni.showModal({
				title: '确定下架？',
				success: res => {
					if (res.confirm == true) {
						uni.request({
							url: this.prefix + "/doOpeRoom",
							data: {
								id: id,
								status: "D"
							},
							success: (res) => {
								if (res.data.code === 200) {
									uni.showToast({
										title: "下架成功",
										icon: "none"
									})
									this.list.splice(index, 1);
								} else {
									uni.showToast({
										title: res.data.msg,
										icon: "none"
									})
								}
							}
						})
					}
				}
			})
		},
		del(id,index) {
			uni.showModal({
				title: '确定删除？',
				success: res => {
					if (res.confirm == true) {
						uni.request({
							url: this.prefix + "/doOpeRoom",
							data: {
								id: id,
								status: "DELETE"
							},
							success: (res) => {
								if (res.data.code === 200) {
									uni.showToast({
										title: "删除成功",
										icon: "none"
									})
									this.list.splice(index, 1);
								} else {
									uni.showToast({
										title: res.data.msg,
										icon: "none"
									})
								}
							}
						})
					}
				}
			})
		},
		push(id,index) {
			uni.showModal({
				title: '确定上架？',
				success: res => {
					if (res.confirm == true) {
						uni.request({
							url: this.prefix + "/doOpeRoom",
							data: {
								id: id,
								status: "FR"
							},
							success: (res) => {
								if (res.data.code === 200) {
									uni.showToast({
										title: "上架成功",
										icon: "none"
									})
									this.list.splice(index, 1);
								} else {
									uni.showToast({
										title: res.data.msg,
										icon: "none"
									})
								}
							}
						})
					}
				}
			})
		},
		jump_edit(id) {
			uni.navigateTo({
				url: "./roomEdit?id=" + id + "&is_edit=true"
			})
		},
		onClickItem(index) {
			if (this.current !== index) {
				this.current = index;
			}
			if (1 === index) {
				this.getRoomList("D")
			} else {
				this.getRoomList("E")
			}
		}
	}
}
